import PressSwitch from 'press-ui/press-switch/press-switch.vue';
import PressDialog from 'press-ui/press-dialog/press-dialog.vue';
import PressDialogHandler from 'press-ui/press-dialog';
import PressCell from 'press-ui/press-cell/press-cell.vue';
import PressCellGroup from 'press-ui/press-cell-group/press-cell-group.vue';


export default {
  i18n: {
    'zh-CN': {
      title: '标题',
      confirm: '提醒',
      message: '是否切换开关？',
      withCell: '搭配单元格使用',
      customSize: '自定义大小',
      customColor: '自定义颜色',
      asyncControl: '异步控制',
    },
    'en-US': {
      title: 'Title',
      confirm: 'Confirm',
      message: 'Are you sure to toggle switch?',
      withCell: 'Inside a Cell',
      customSize: 'Custom Size',
      customColor: 'Custom Color',
      asyncControl: 'Async Control',
    },
  },

  components: {
    PressSwitch,
    PressDialog,
    PressCell,
    PressCellGroup,
  },
  data() {
    return {
      isOpen: true,
      checkedMap: {
        basic: true,
        color: true,
        size: true,
        async: true,
        cell: true,
      },
    };
  },
  methods: {
    onSwitchChange() {
      this.isOpen = !this.isOpen;
    },
    onChange(value, type) {
      console.log('[value]', value, type);

      if (type === 'async') {
        PressDialogHandler.show({
          context: this,
          title: this.t('title'),
          content: this.t('message'),
          confirmText: this.t('confirm'),
          cancelText: this.t('cancel'),
        }).then(() => {
          this.checkedMap.async = value;
        });
        return;
      }
      this.checkedMap[type] = value;
    },
    onClick() {
      this.onGTip('[click]');
    },
  },
};
